body {
  background: #222;
}
.banner {
  min-height: 100vh;
  background: #120e0f url(../img/bg.webp);
  background-position: center;
  background-size: cover;
  padding: 40px 100px;
  display: flex;
  align-items: center;
}
header {
  position: absolute;
  top: 40px;
  display: flex;
  align-items: center;
}
.logo img {
  max-width: 120px;
}
.toggle {
  position: relative;
  width: 50px;
  height: 50px;
  background: #fff;
  margin-right: 20px;
  cursor: pointer;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}
.toggle::before {
  content: "";
  position: absolute;
  width: 28px;
  height: 3px;
  background: #120e0f;
  transform: translateY(-6px);
  transition: 0.5s;
}
.toggle.active::before {
  transform: translateY(0) rotate(45deg);
}
.toggle::after {
  content: "";
  position: absolute;
  width: 28px;
  height: 3px;
  background: #120e0f;
  transform: translateY(6px);
  transition: 0.5s;
}
.toggle.active::after {
  transform: translateY(0) rotate(-45deg);
}
.navigation {
  position: fixed;
  z-index: 9;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #120e0f url(../img/menuBg.webp);
  background-position: right;
  background-size: cover;
  padding-right: 100px;
  display: none;
}
.navigation.active {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
}
.navigation li {
  list-style: none;
  margin: 4px 0;
}
.navigation li a {
  color: #fff;
  letter-spacing: 0.05em;
  font-size: 2em;
  text-decoration: none;
  font-weight: 600;
  text-transform: uppercase;
}
.navigation li a:hover {
  color: #e83c80;
}
.content {
  position: relative;
  max-width: 700px;
}
.content .textimg {
  max-width: 600px;
  width: 100%;
}
.content p {
  color: #fff;
  font-size: 1.2em;
  line-height: 1.75em;
  font-weight: 300;
}
.content .btn {
  position: relative;
  background: #e83c80;
  padding: 10px 18px;
  display: inline-flex;
  align-items: center;
  margin-top: 20px;
  font-size: 1.1em;
  font-weight: 400;
  text-decoration: none;
  color: #fff;
}
.btn:hover {
  background: #d43071;
}
.btn ion-icon {
  font-size: 2em;
  margin-right: 5px;
}
.clip {
  position: absolute;
  right: 100px;
  bottom: 40px;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}
.clip .clipBox {
  position: relative;
  width: 340px;
  height: 220px;
  border: 8px solid #fff;
  border-radius: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: #e83c80;
}
.clip .clipBox img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.clip .clipBox ion-icon {
  font-size: 4em;
  color: #fff;
}
iframe {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
iframe:hover {
  opacity: 1;
  transition: all 0.2s ease-in-out;
}
.socialIcon {
  position: absolute;
  bottom: 40px;
  left: 100px;
  display: flex;
  align-items: center;
  gap: 20px;
}
.socialIcon li {
  list-style: none;
}
.socialIcon li a {
  font-size: 2em;
  color: #fff;
  transition: all 0.2s ease-in-out;
}
.socialIcon li a:hover {
  color: #e83c80;
}
@media (max-width: 1119px) {
  .banner {
    padding: 40px;
    height: 900px;
    min-height: 100vh;
    align-items: flex-start;
  }
  .content {
    top: 120px;
  }
  .clip {
    top: 540px;
    right: 0;
    left: 40px;
  }
  .socialIcon {
    top: 800px;
    left: 40px;
  }
}
@media (max-width: 790px) {
  .content {
    top: 20px;
  }
  .clip {
    position: relative;
    right: initial;
    bottom: initial;
    top: 340px;
    left: 0;
  }
  .banner {
    padding: 15px;
  }
  header {
    right: 15px;
  }
  .toggle.active {
    order: 2;
    transition: all 0.4s ease-in-out;
  }
}
@media (max-width: 599px) {
  .content {
    top: 30px;
    position: absolute;
  }
  .clip {
    top: 350px;
    gap: 20px;
  }
  .content p {
    font-size: 1em;
  }
  .socialIcon {
    top: 860px;
    left: 15px;
  }
  header {
    top: 15px;
  }

  .banner {
    padding-top: 0px;
  }
}
@media (max-width: 359px) {
  .clip {
    width: 300px;
  }
}
